"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Upload } from "lucide-react";

export default function DashboardPage() {
  const [file, setFile] = useState(null);

  // Handle file selection
  const handleFileChange = (event) => {
    const selectedFile = event.target.files[0];
    if (
      selectedFile &&
      (selectedFile.type === "application/pdf" ||
        selectedFile.type ===
          "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        selectedFile.type === "application/msword")
    ) {
      setFile(selectedFile);
    } else {
      alert("Please upload a valid PDF or DOC file.");
      setFile(null);
    }
  };

  // Handle form submission
  const handleSubmit = (event) => {
    event.preventDefault();
    if (file) {
      // Logic for file submission (e.g., send to backend or process the file)
      console.log("File submitted:", file.name);
      // You can add API calls or further processing here
    } else {
      alert("Please select a file to upload.");
    }
  };

  return (
    <div className="max-w-[100vw] overflow-x-hidden flex items-center justify-center min-h-screen bg-gray-50">
      <Card className="w-full max-w-md mx-auto bg-white border border-gray-100 shadow-sm">
        <CardHeader>
          <CardTitle className="text-2xl text-center">
            Syllabus Extraction
          </CardTitle>
        </CardHeader>
        <CardContent className="p-6">
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="flex flex-col items-center space-y-4">
              <label
                htmlFor="file-upload"
                className="flex flex-col items-center justify-center w-full h-64 border-2 border-dashed border-gray-300 rounded-lg cursor-pointer hover:bg-gray-50"
              >
                <div className="flex flex-col items-center space-y-2">
                  <Upload className="w-12 h-12 text-gray-400" />
                  <p className="text-sm text-gray-600">
                    {file ? file.name : "Upload PDF or DOC file"}
                  </p>
                  <p className="text-xs text-gray-400">
                    Click to browse or drag and drop
                  </p>
                </div>
                <input
                  id="file-upload"
                  type="file"
                  accept=".pdf,.doc,.docx"
                  onChange={handleFileChange}
                  className="hidden"
                />
              </label>
            </div>
            <Button
              type="submit"
              className="w-full bg-red-500 hover:bg-red-600 text-white"
              disabled={!file}
            >
              Submit
            </Button>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}